import createDataContext from './createDataContext';

const MSGS = {
	THUMB_POS: 'THUMB_POS',
	CLICK_POS: 'CLICK_POS',
	INIT_WIDTH_POS: 'INIT_WIDTH_POS',
	CONTAINER_WIDTH: 'CONTAINER_WIDTH'
};

const containerWidthMsg = (dispatch) => (width) => {
	dispatch({
		type: MSGS.CONTAINER_WIDTH,
		width
	});
};

const thumbPosMsg = (dispatch) => (pos) => {
	dispatch({
		type: MSGS.THUMB_POS,
		pos
	});
};
const initWidthPosMsg = (dispatch) => (width) => {
	dispatch({
		type: MSGS.INIT_WIDTH_POS,
		width
	});
};

const thumbClickPosMsg = (dispatch) => (clickPos) => {
	dispatch({
		type: MSGS.CLICK_POS,
		clickPos
	});
};

const BookingContext = (state, msg) => {
	switch (msg.type) {
		case MSGS.CONTAINER_WIDTH: {
			const { width } = msg;

			return { ...state, width };
		}

		case MSGS.THUMB_POS: {
			const { pos } = msg;
			const prevPos = pos;

			return { ...state, pos };
		}

		default:
			return state;
	}
};
export const { Context, Provider } = createDataContext(
	BookingContext,
	{
		thumbPosMsg,
		initWidthPosMsg,
		thumbClickPosMsg,
		containerWidthMsg
	},
	{
		pos: 0,
		width: 0,
		clickPos: 0
	}
);
