import React, { useId, useContext, useState, useRef } from 'react';

import './Grid.css';

import { Rented } from './Rented';
export const GridCell = ({ reference, rented, className, index, w, onDClick }) => {
	return index !== 13 ? (
		<div onDoubleClick={onDClick} ref={reference} className={className}>
			{rented ? <Rented index={index} id={rented.id} className={'draggable '} w={w} rented={rented} /> : null}
		</div>
	) : (
		<div ref={reference} className="grid1" />
	);
};
