import React, { useContext, useEffect, useState, useRef } from 'react';
import { Bar } from './Bar';
import { Context } from '../../context/ThumbContext';
import { Context as DatesContext } from '../../context/DatesContext';

export const ScrollBar = () => {
	const intervalRef = useRef(null);

	const [ counter, setCounter ] = useState(0);
	const [ counter2, setCounter2 ] = useState(0);
	const [ bmDown, setbmDown ] = useState(false);

	const { addIntervalMsg } = useContext(DatesContext);

	const { state, thumbPosMsg } = useContext(Context);
	const { pos } = state;

	// useEffect(
	//     () => {
	//         addIntervalMsg('+');
	//         thumbPosMsg(pos + 5);
	//     },
	//     [counter]
	// );
	// useEffect(
	//     () => {
	//         subIntervalMsg('-');
	//         thumbPosMsg(pos - 5);
	//     },
	//     [counter2]
	// );

	const startCounter = () => {
		setbmDown(true);
		if (intervalRef.current) return;

		intervalRef.current = setInterval(() => {
			setCounter((pos) => pos + 1);
		}, 50);
	};
	const stopCounter = () => {
		if (intervalRef.current) {
			clearInterval(intervalRef.current);
			intervalRef.current = null;
		}
	};

	const startCounter2 = () => {
		addIntervalMsg('+');
		setbmDown(true);
		if (intervalRef.current) return;
		intervalRef.current = setInterval(() => {
			setCounter2((pos) => pos + 1);
		}, 50);
	};
	const stopCounter2 = () => {
		if (intervalRef.current) {
			clearInterval(intervalRef.current);
			intervalRef.current = null;
		}
	};

	const mouseEnter = () => {
		setbmDown(false);
	};
	const mouseLeave = () => {
		bmDown ? thumbPosMsg(0) : thumbPosMsg(pos);
	};

	return (
		<div style={{ display: 'flex', width: '100%' }}>
			<div
				onMouseDown={startCounter2}
				onMouseUp={stopCounter2}
				onMouseEnter={mouseEnter}
				onMouseLeave={mouseLeave}
			/>
			<Bar />
			<div onMouseDown={startCounter} onMouseUp={stopCounter} onMouseEnter={mouseEnter} onMouseLeave={mouseLeave}>
				hola
			</div>
		</div>
	);
};
